@font-face {
  font-family: 'M PLUS 1p';
  src: url("../fonts/mplus1p-medium.woff2") format("woff2"), url("../fonts/mplus1p-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'M PLUS 1p';
  src: url("../fonts/mplus1p-regular.woff2") format("woff2"), url("../fonts/mplus1p-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box;
  height: 100%;
  font-family: 'M PLUS 1p','Arial Black', 'Helvetica CY', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  background-color: #0b0a0d; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  color: inherit;
  text-decoration: none; }
  a:visited {
    color: inherit; }

img {
  max-width: 100%; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0 none;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%); }

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1240px; }

.header__wrapper {
  padding-top: 77px;
  padding-bottom: 77px;
  display: flex; }

.header__logo {
  margin-right: 213px;
  transition: opacity 0.3s ease; }
  @media (hover: hover) {
    .header__logo:hover:not(.focus-visible) {
      opacity: 0.7; } }
  .header__logo.focus-visible:focus {
    opacity: 0.7; }
  .header__logo svg {
    width: 42px;
    height: 50px; }

.header__navigation-list {
  margin: 0;
  padding-left: 0;
  display: flex;
  list-style: none;
  gap: 30px; }

.header__sub-navigation {
  position: absolute;
  left: 0;
  top: 100%;
  margin-bottom: 0;
  padding-top: 95px;
  padding-left: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  -moz-column-gap: 142px;
       column-gap: 142px;
  row-gap: 30px;
  list-style: none;
  max-width: 100vw;
  background-color: #0b0a0d;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease; }
  @media (hover: hover) {
    .header__sub-navigation:hover:not(.focus-visible) {
      opacity: 1;
      visibility: visible; } }
  .header__sub-navigation.focus-visible:focus {
    opacity: 1;
    visibility: visible; }

.header__sub-navigation-item {
  width: -moz-max-content;
  width: max-content;
  max-width: 100%; }

.header__navigation-item {
  position: relative; }
  @media (hover: hover) {
    .header__navigation-item:hover:not(.focus-visible) .header__sub-navigation {
      opacity: 1;
      visibility: visible; } }
  .header__navigation-item.focus-visible:focus .header__sub-navigation {
    opacity: 1;
    visibility: visible; }

.link {
  position: relative;
  color: inherit;
  transition: color 0.3 ease; }
  .link::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -20px;
    width: 0;
    height: 1px;
    background-color: #ffffff;
    transition: width 0.3s ease; }
  @media (hover: hover) {
    .link:hover:not(.focus-visible) {
      color: #75ff72; }
      .link:hover:not(.focus-visible):active {
        color: #ffffff; }
        .link:hover:not(.focus-visible):active::after {
          width: 100%; } }
  .link.focus-visible:focus {
    color: #75ff72; }
    .link.focus-visible:focus:active {
      color: #ffffff; }
      .link.focus-visible:focus:active::after {
        width: 100%; }
  .link:active {
    color: #ffffff; }
    .link:active::after {
      width: 100%; }
